<template>
  <div >
    <el-form :model="passForm" :rules="rules" class="change-container" label-position="left"
             label-width="0px" v-loading="loading">
      <h3 class="change_title">修改密码</h3>
      <el-form-item prop="oldpass">
        <el-input type="password" v-model="passForm.oldpass"
                  auto-complete="off" placeholder="旧密码"></el-input>
      </el-form-item>
      <el-form-item prop="newpass">
        <el-input type="password" v-model="passForm.newpass"
                  auto-complete="off" placeholder="新密码"></el-input>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 40%;background: #505458;border: none" v-on:click="change">更改</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  const axios = require('axios')
  export default {
    name: 'charge',
    inject: ['user'],
    data () {
      return {
        rules: {
          oldpass: [{required: true, message: '密码不能为空', trigger: 'blur'}],
          newpass: [{required: true, message: '密码不能为空', trigger: 'blur'}]
        },
        checked: true,
        passForm: {
          oldpass: '',
          newpass: ''
        },
        loading: false
      }
    },

    methods: {
      change () {
        axios({
          method: 'post',
          url: '/index?action=change',
          data: {'uid': this.user.uid,'oldpass': this.passForm.oldpass, 'newpass': this.passForm.newpass},
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
          if (resp.data.result === false) {
            this.$alert('更改失败！请检查是否输入错误！', '提示', {
              confirmButtonText: '确定'
            })
          } else {
            this.$alert('更改成功', '提示', {
              confirmButtonText: '确定'
            })
          }
        }).catch(failResponse => {})
      }
    }
  }
</script>

<style scoped>
  .change-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .change_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
</style>



